import React from 'react'
import './About.css'
import { HashLink as Link} from 'react-router-hash-link'
import Video from './video.mp4'
import Image1 from './123.jpg'

const About = () => {
    return (
        <div className="col-lg-12 col-md-12 col-12 about-container">
            <div className="about-inner">
                <h1 className="text-center">About Us&nbsp;<i class="fas fa-arrow-down" style={{color:'#ff691f'}}></i></h1>
            </div>
            <div className="about-inner2">
                <div className="col-lg-3 col-md-4 col-12 part-1">
                    <small>GIVE IT A READ</small>
                    <h3>What you need to know about us!</h3>
                    <p>From creating websites, apps to digital branding and online campaigns for companies. We believe in close collaboration, long- term, nurtured business relationships and a multidisciplinary culture which sets us apart from the rest.We are specifically focused on helping you achieve your commercial objectives in the best possible way. By working together with our clients, our ever-hard-working team will go the extra mile to bring your vision to life and help your brand resonate with your audiences ensuring the highest quality services and a profitable ROI.</p>
                </div>

                <div className="col-lg-3 col-md-4 col-10 part-2">
                    <video src={Video} autoPlay playsInline muted loop className="part-2-video"/>
                </div>

                <div className="col-lg-3 col-md-4 col-10 part-3">
                    <img src={Image1} className="part-3-image img-fluid"/>
                    <div className="hero-btns2">
                        <Link to="#popos" smooth={true} duration={500} spy={true} exact='true' offset={-80}> <button
                        className="btn--outline2"
                        >Learn More</button></Link>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default About
