import React from 'react'
import './Contact.css'
import { HashLink as Link} from 'react-router-hash-link'
import Image22 from './cu.jpg'
import CC2 from './chat.svg'

const Contact = () => {
    return (
        <div className="contact-container">
            <div className="contact-inner">
                <h1 className="text-center">Get in touch with us!&nbsp;<i class="fas fa-arrow-down" style={{color:'#ff691f'}}></i></h1>
            </div>
            <div className="row contact-inner-2">
                <div className="col-lg-6 col-md-6 col-12">
                    <img src={Image22} className="cu-img img-fluid" />
                </div>

                <div className="col-lg-6 col-md-6 col-12 contact-inner-3">
                <img src={CC2} className="chat-img"/>
                   <h3>“To earn the respect (and eventually love) of your customers, you first have to respect those customers. That is why Golden Rule behavior is embraced by most of the winning companies.” – Colleen Barrett, Southwest Airlines President Emerita</h3>
                    <div className="hero-btns">
                        <Link to="/contactus" smooth={true} duration={500} spy={true} exact='true' offset={-80}> <button
                        className="btn--outline4"
                        >Contact Us</button></Link>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Contact
