import React from 'react'
import './Director.css'
import { HashLink as Link} from 'react-router-hash-link'

const Director = () => {
    return (
        <div className="row justify-content-center dircon">
        <div className="col-lg-8 md-8 col-10 dirinner">
            <h3>Here is what the Director has to say to you!</h3>
            <p>Progress for me has never been a dream. It has always been a distant reality and once one milestone is achieved, there is always another one waiting to be crossed. It is with this belief that I started Arfin India Ltd. in 2001, a company that was in the business of Aluminum Recycling. Almost instantly, Arfin India Ltd. became a prominent name in its business segment, but like every entrepreneur sticks to their dream, I always wanted to push my company to more prosperous milestones. More than being a global player, I always perceived my company as a unit that could make a difference with its quality and competitively priced products. Taking a path less traveled, I expanded Arfin into Ferro Alloys production, a highly sensitive business that requires a high amount of technical know-how, expertise and specialized knowledge.</p>
            <div className="hero-btns">
                 <Link to="/aboutus" smooth={true} duration={500} spy={true} exact='true' offset={-80}> <button
                 className="btn--outline"
                 >Learn More</button></Link>
            </div>
        </div>
        </div>
    )
}

export default Director
