import Navbar from './Navbar.js'
import './App.css';
import {BrowserRouter as Router, Switch, Route} from 'react-router-dom'
import AboutUs from './AboutUs.js';
import Home from './Home.js';
import ScrollToTop from './ScrollToTop.js';
import CC from './CC.js';
import WebDev from './WebDev.js'
import DM from './DM.js';
import WD from './WD.js';
import ContactUs from './ContactUs.js';
import './App.css'

function App() {
  return (
   <>
    <Router>
      <ScrollToTop>
    <Navbar/>
      <Switch>
      <Route path='/newdm' exact component={Home} />
      <Route path='/aboutus' exact component={AboutUs} />
      <Route path='/cc' exact component={CC} />
      <Route path='/webdev' exact component={WebDev} />
      <Route path='/dm' exact component={DM} />
      <Route path='/wd' exact component={WD} />
      <Route path='/contactus' exact component={ContactUs} />
        </Switch>
      </ScrollToTop>
    </Router>
   </>
  );
}

export default App;
