import React from 'react'
import './HeroSection.css';
import Video from './BG.jpg'

import { HashLink as Link} from 'react-router-hash-link'

const HeroSection = () => {
    return (
        <div className="hero-container" style={{background:`url("${Video}")`, backgroundSize:'cover'}}>
            <h5>We are a technological and marketing company based in India. </h5>
            <p>We offer a variety of services such as Web Development, Web Design, Digital Marketing and Call Center Services. Contact us with your details and the kind of project you are interested to go forward with!</p>
        </div>
    );
}

export default HeroSection